/**
 * Create reverse lookup for namespace elements
 */
export const namespaceElements: Record<string | symbol, string> = Object
  .entries(
    namespaceElementsMap,
  ).reduce(
    (acc, [namespace, elements]) => {
      elements.forEach((element) => {
        if (!(element in acc)) acc[element] = namespace;
      });
      return acc;
    },
    {} as Record<string | symbol, string>,
  );
